<template>

	<yd-layout>
		<yd-navbar slot="navbar" title="回复邮件">
			<a @click="backToMailDetail" slot="left">
				<span>取消</span>
			</a>
			<a slot="right">
				<span class="sendBtn" @click="sendMail">发送</span>
			</a>
		</yd-navbar>
		<yd-popup v-model="showUserDialog" position="center" width="100%">
			<rx-sysuser single=false ref="sysUser" @onOk="selectUser" @onCancel="showUserDialog=false;"></rx-sysuser>
		</yd-popup>
		<div class="main-container">
			<ul>
				<li>
					<h1>收件人：</h1>					
					<rx-boxs v-model="data.receiver" containstyle="width:2.2rem;" textField="text" @select="selectUserDialog" valField="id" v-if="mailType=='reply'||mailType=='transmit'"></rx-boxs>					
					
				</li>			
				<li v-if="needCopySend">
					<h1>抄送：</h1>
					<rx-boxs v-model="data.copyReceiver" containstyle="width:2.2rem;" textField="text" @select="selectUserDialog" valField="id" v-if="mailType=='reply'||mailType=='transmit'"></rx-boxs>
				</li>				
				<li v-if="needCopySend">
					<h1>发件人：</h1><p>{{loginUser.fullname}}</p>
				</li>				
				<li @click="checkSender" v-if="!needCopySend">
					<h1>抄送,发件人：</h1><p>{{loginUser.fullname}}</p>
				</li>
				<li>
					<h1>主题：</h1><p>{{mailObject.subject}}</p>
				</li>
				<li>
					<h1>紧急程度：</h1>
					<p>
						<yd-radio-group v-model="mailUrge">
					        <yd-radio val="1">一般</yd-radio>
					        <yd-radio val="2">重要</yd-radio>
					        <yd-radio val="3">非常重要</yd-radio>
					   </yd-radio-group>					
					</p>					
				</li>				
			</ul>
			<div>
				<yd-cell-group title="回复内容">
					<yd-cell-item>
						<yd-textarea slot="right" maxlength="500" v-model="replyContent"></yd-textarea>
					</yd-cell-item>
				</yd-cell-group>
			</div>
			<div v-if="mailType=='reply'" v-once>			
				<p class="originTip-title">--原始邮件--</p>
				<dl class="originTip">
					<dd><p>发件人：{{mailObject.sender}}</p></dd>
					<dd><p>发送时间：{{mailObject.senderTime}}</p></dd>
					<dd><p>收件人：{{mailObject.recNames}}</p></dd>
					<dd><p>主题：{{mailObject.subject}}</p></dd>
				</dl>
				<p class="originMail">
					原始内容： {{mailObject.content}}
				</p>			
			</div>
		</div>
	</yd-layout>

</template>

<script>
	import Vue from 'vue';
	import { RxUtil } from "@/assets/util.js";
	import {Radio, RadioGroup} from 'vue-ydui/dist/lib.rem/radio';
	import router from '@/router/index';
	import sysuser from '@/components/common/SysUser';
	import rxboxs from '@/components/common/rx-boxs';
	Vue.component(sysuser.name, sysuser);
	Vue.component(rxboxs.name, rxboxs);
	
export default {

	data() {
		return {
			mailUrge:"1",
			mailObject: this.$route.params.mailObject,
			loginUser: {},
			replyContent:"",
			mailType : this.$route.params.mailType,
			operation:this.$route.params.mailType,
			orginMail:{},
			needCopySend:false,
			data: {
				receiver:this.$route.params.recUser,
				copyReceiver: "[]"
			},
			showUserDialog:false

		}
	},
	methods: {
		getInfo() {
			var url = _baseUrl + '/mobile/oa/personalInfo/get.do';
			this.$ajax(url).then(response => {
					this.loginUser = response.data.user;
			}).catch(error => {
						console.log(error);
			});
		},
		sendMail(){
			let recIdsData = JSON.parse(this.data.receiver) || [];	
			if(recIdsData.length==0){
				this.errorToast("请选择收件人");		
				return;
			}	
			let copyIdsData = JSON.parse(this.data.copyReceiver) || [];						
			let recIdsArray = [];	
			let recNamesArray = [];
			let copyIdsArray = [];
			let copyNamesArray = [];
			
			
			
			for(var i = 0 ; i < recIdsData.length ; i++){
				recIdsArray.push(recIdsData[i].id);
				recNamesArray.push(recIdsData[i].text);
			}	
			this.mailObject.recIds = recIdsArray.join(",");
			this.mailObject.recNames = recNamesArray.join(",");
			
			
			if(copyIdsData.length!=0){
				for(var i = 0 ; i < copyIdsData.length ; i++){
					copyIdsArray.push(copyIdsData[i].id);
					copyNamesArray.push(copyIdsData[i].text);
				}				
				this.mailObject.ccIds = copyIdsArray.join(",");
				this.mailObject.ccNames = copyNamesArray.join(",");
			}
			
			this.mailObject.content = this.replyContent;
			var url = _baseUrl + '/mobile/oa/sendMail.do';
			this.$ajax({	
				url:url,
				method:'POST',
				params:{
					data:this.mailObject,
					urge:this.mailUrge,					
				}
			}).then(response => {
				if(response.data.success){
					this.successToast("发送邮件成功",1500);
				}
				router.push({name:'mailBox'});
			}).catch(error => {
				console.log(error);				
			});
		},
		backToMailDetail(){
			router.push({name:'innerMailDetail',params:{mail:this.mailObject,mailType:'recieve'}});
		},		
		showContent(){
			var url = _baseUrl + '/mobile/oa/editMail.do';
			this.$ajax({	
				url:url,
				method:'POST',
				params:{
					pkId:this.mailObject.mailId,
					operation:this.operation,					
				}
			}).then(response => {
				this.mailObject = response.data.data;
			}).catch(error => {
				console.log(error);
			});
		},
		checkSender(){
			//alert("点击了发件人");
			this.needCopySend = true;
		},
		selectUserDialog(vm) {
			var selectUser = vm.getIdVal(false);
			this.showUserDialog = true;
			this.$refs.sysUser.search();
			// 设置选择用户。
			this.$refs.sysUser.setValue("");
			//设置选中的rx-box 控件，用于传递数据。
			this.nodeUserVm = vm;
		},
		selectUser(data) {
			var list = [];
			for(var i = 0; i < data.length; i++) {
				var obj = data[i];
				var o = {};
				o.id = obj.userId;
				o.text = obj.fullname;
				list.push(o);
			}
			var json = JSON.stringify(list);
			this.nodeUserVm.setValue(json);
			this.showUserDialog = false;
		}
	},
	created() {
		this.getInfo();		
		this.showContent();
		console.info(this.$route.params.recUser);
	}

}
</script>

<style scoped>
.clearfix{
	clear: both;
}

dl,dd{
	margin: 0;
	padding: 0;
}

ul{
	margin: 0;
}

li{
	list-style: none;
}

.sendBtn {
	color: #065279;
	font-weight: bold;
}
.main-container{
	background-color: #FFFFFF;
}

.main-container ul li{
	border-bottom: 1px solid #F5F5F5;
	padding-left:0.2rem;
}

.main-container ul li::after{
	content: '';
	display: block;
	clear:both;
}

.main-container ul li h1,.main-container ul li p,.main-container ul li input
{
	float: left;
	margin: 0.15rem auto;
	font-size: 0.3rem;
}


.main-container ul li h1{
	font-size: 0.3rem;
	color: #ccc;
}

.main-container ul li p{
	color: #666;
	margin-left: 0.2rem;
}

.originTip{
	background: #F5F5F5;
	border-radius: 2px;
	padding: 0.1rem;
	width: 95%;
	margin: auto;
}

.originTip dd p{
	line-height: 0.4rem;
	font-weight: bold;
}

.originTip-title{
	padding-left: 0.2rem;
	margin-bottom: 0.3rem;
}

.originMail{
	width: 95%;
	margin: 1.5rem auto 0 auto;
}

.mailUrge-radio{
	margin: 0.1rem auto 0.1rem auto;
}
</style>


